import { Component, Output, Input, forwardRef, OnInit, EventEmitter } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { trigger, state, style, AUTO_STYLE, transition, animate } from '@angular/animations'

@Component({
  selector: 'hank-dropdown',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => HankDropdownComponent),
      multi: true
    }
  ],
  templateUrl: './hank-dropdown.component.html',
  styleUrls: ['./hank-dropdown.component.css'],
  animations: [
    trigger('divState',[
      state('expanded',style({
          height: AUTO_STYLE
      })),
      state('collapsed',style({
          height: 0,
          display: 'none'
       })),
       transition('collapsed => expanded',animate('150ms ease-out'))
    ]),
  ]
})

export class HankDropdownComponent implements OnInit, ControlValueAccessor {
  @Input('id')id = ''; //éè¿parent componetç»è¿ä¸ªdropdownå½å
  @Input('options')options = [];
  @Input('placeholder')placeholder = '';
  @Input('disabled') disabled: boolean = false;
  @Input('width') width: string;
  @Input('max-height') maxHeight: string;
  @Output() selected = new EventEmitter<{id:string,value:string}>(); //æidåä»dropdownä¸­éä¸­çå¼ä¼ ç»parent componentã
  search:string='';
  showDropdown:string = 'collapsed';
  dropdownWidth:string;

  ngOnInit() {
    this.dropdownWidth = parseInt(this.width)+6+'px';
    this.width+='px';
    this.maxHeight+='px';
  }

  show() {
    this.showDropdown = 'expanded';
  }

  hide() {
    this.onTouched();
    setTimeout(()=>this.showDropdown = 'collapsed', 150);
  }

  detectInput(event:Event) {
    this.search = (<HTMLInputElement>event.target).value;
    for(let option of this.options) {
      let splited = option.split(' ');
      for(let str of splited) {
        if(str.indexOf(this.search)===0 || option.indexOf(this.search)===0) {
          this.showDropdown = 'expanded';
          return;
        } 
      }
    }
    this.showDropdown = 'collapsed'; //éåå®datasï¼ä¸åæ¬è¾å¥ï¼ä¸æ¾ç¤ºdropdown
  }

  select(option:string) {
    this.search = option; //æéä¸­çå¼éåè¾å¥æ¡
    this.selected.emit({id:this.id,value:option}); //éä¸­çå¼ä¼ ç»parennt component
    this.showDropdown = 'collapsed'; //å³é­dropdown
    this.onChange(this.search); //ç»reactive formä¼ å¼
  }

  /* --------------  form control ---------------*/

  onChange = (value) => {}

  onTouched = () => {}

  registerOnChange(fn) {
    this.onChange = fn; 
  }

  registerOnTouched(fn) {
    this.onTouched = fn;
  }

  writeValue(value) {
    this.search = value ? value : ''; //reactive form ç»searchè®¾åå¼
  }

  // setDisabledState(isDisabled: boolean){
  //   this.disabled = isDisabled;
  // }
}

